import React from "react";
import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";
import { Calendar } from "primereact/calendar";
import { Button } from "primereact/button";
import { Dropdown } from "primereact/dropdown";
import { useForm, Controller } from "react-hook-form";
import { addCompany } from "../../apis/Companies";
import constants from "../../constants";
import utils from "../../utils";

const AddCompanyForm = ({ showAddCompany, setShowAddCompany, toast, refresh }) => {
  const { errors, handleSubmit, control } = useForm();
  const [loading, setLoading] = React.useState(false);
  const minimumTaxTypeSelectItems = [
    { label: "Old", value: 0 },
    { label: "New", value: 1 }
  ];

  const onSubmit = async data => {
    if (loading) return;

    setLoading(true);
    const {
      companyName,
      cacNumber,
      tinNumber,
      companyDescription,
      openingYear,
      monthOfOperation,
      unRelievedCf,
      lossCf,
      deferredTaxBroughtFoward,
      minimumTaxTypeId
    } = data;
    try {
      const response = await addCompany({
        companyName,
        cacNumber,
        tinNumber,
        companyDescription,
        openingYear,
        monthOfOperation,
        unRelievedCf,
        lossCf,
        deferredTaxBroughtFoward,
        minimumTaxTypeId
      });
      if (response.status === 201) {
        toast.show(
          utils.toastCallback({ severity: "success", detail: "Company added successfully" })
        );
        setTimeout(() => {
          setLoading(false);
          setShowAddCompany(false);
          refresh();
        }, 2000);
      }
    } catch (error) {
      setLoading(false);
      if (error.response) {
        const {
          data: { errors }
        } = error.response;
        // display all errors as toast notification
        errors?.companyError &&
          errors.error.companyError.map(error =>
            toast.show(utils.toastCallback({ severity: "error", summary: "Error", detail: error }))
          );
        errors?.CompanyDescription &&
          errors.CompanyDescription.map(error =>
            toast.show(utils.toastCallback({ severity: "error", summary: "Error", detail: error }))
          );
        errors?.deferredTaxBroughtFoward &&
          errors.deferredTaxBroughtFoward.map(error =>
            toast.show(utils.toastCallback({ severity: "error", summary: "Error", detail: error }))
          );
        errors?.lossCf &&
          errors.lossCf.map(error =>
            toast.show(utils.toastCallback({ severity: "error", summary: "Error", detail: error }))
          );
        errors?.unRelievedCf &&
          errors.unRelievedCf.map(error =>
            toast.show(utils.toastCallback({ severity: "error", summary: "Error", detail: error }))
          );
        errors?.monthOfOperation &&
          errors.monthOfOperation.map(error =>
            toast.show(utils.toastCallback({ severity: "error", summary: "Error", detail: error }))
          );
        if (errors.length) {
          errors.forEach(error =>
            toast.show(utils.toastCallback({ severity: "error", summary: "Error", detail: error }))
          );
        }
      } else {
        // network errors
        toast.show(
          utils.toastCallback({
            summary: "Network Error",
            detail: constants.networkErrorMessage
          })
        );
      }
    }
  };

  return (
    <Dialog
      header="Add Company"
      visible={showAddCompany}
      style={{ width: 450 }}
      focusOnShow={false}
      onHide={() => setShowAddCompany(false)}>
      <form onSubmit={handleSubmit(onSubmit)} className="p-d-flex p-flex-column p-jc-center">
        <div className="p-d-flex p-flex-column" style={{ marginBottom: 15 }}>
          <label htmlFor="companyNameInput" style={{ marginBottom: 10 }}>
            Company Name
          </label>
          <Controller
            name="companyName"
            control={control}
            rules={{ required: true }}
            defaultValue=""
            render={props => (
              <InputText
                style={{ marginBottom: 5, width: "100%" }}
                id="companyNameInput"
                value={props.value}
                onChange={e => props.onChange(e.target.value)}
              />
            )}
          />
          {errors.companyName && (
            <span style={{ fontSize: 12, color: "red" }}>Company Name is required</span>
          )}
        </div>
        <div className="p-d-flex">
          <div className="p-d-flex p-flex-column" style={{ marginBottom: 15, marginRight: 10 }}>
            <label htmlFor="cacNumberInput" style={{ marginBottom: 10 }}>
              Cac Number
            </label>
            <Controller
              name="cacNumber"
              control={control}
              rules={{ required: true }}
              defaultValue=""
              render={props => (
                <InputText
                  style={{ marginBottom: 5, width: "100%" }}
                  id="cacNumberInput"
                  value={props.value}
                  onChange={e => props.onChange(e.target.value)}
                />
              )}
            />
            {errors.cacNumber && (
              <span style={{ fontSize: 12, color: "red" }}>Cac Number is required</span>
            )}
          </div>
          <div className="p-d-flex p-flex-column" style={{ marginBottom: 15, marginLeft: 10 }}>
            <label htmlFor="tinNumberInput" style={{ marginBottom: 10 }}>
              Tin Number
            </label>
            <Controller
              name="tinNumber"
              control={control}
              rules={{ required: true }}
              defaultValue=""
              render={props => (
                <InputText
                  style={{ marginBottom: 5, width: "100%" }}
                  id="tinNumberInput"
                  value={props.value}
                  onChange={e => props.onChange(e.target.value)}
                />
              )}
            />
            {errors.tinNumber && (
              <span style={{ fontSize: 12, color: "red" }}>Tin Number is required</span>
            )}
          </div>
        </div>
        <div className="p-d-flex">
          <div className="p-d-flex p-flex-column" style={{ marginBottom: 15, marginRight: 10 }}>
            <label htmlFor="unRelievedCfInput" style={{ marginBottom: 10 }}>
              Unrelieved Capital Allowance b/f
            </label>
            <Controller
              name="unRelievedCf"
              control={control}
              rules={{ required: true }}
              defaultValue=""
              render={props => (
                <InputText
                  style={{ marginBottom: 5, width: "100%" }}
                  id="unRelievedCfInput"
                  value={props.value}
                  onChange={e => props.onChange(e.target.value)}
                />
              )}
            />
            {errors.unRelievedCf && (
              <span style={{ fontSize: 12, color: "red" }}>Unrelieved CA b/f is required</span>
            )}
          </div>
          <div className="p-d-flex p-flex-column" style={{ marginBottom: 15, marginLeft: 10 }}>
            <label htmlFor="lossCfInput" style={{ marginBottom: 10 }}>
              Asessable Loss b/f
            </label>
            <Controller
              name="lossCf"
              control={control}
              rules={{ required: true }}
              defaultValue=""
              render={props => (
                <InputText
                  style={{ marginBottom: 5, width: "100%" }}
                  id="lossCfInput"
                  value={props.value}
                  onChange={e => props.onChange(e.target.value)}
                />
              )}
            />
            {errors.lossCf && (
              <span style={{ fontSize: 12, color: "red" }}>Loss C/f is required</span>
            )}
          </div>
        </div>
        <div className="p-d-flex p-flex-column" style={{ marginBottom: 15 }}>
          <label htmlFor="minimumTaxTypeIdInput" style={{ marginBottom: 10 }}>
            Minimum Tax Type
          </label>
          <Controller
            name="minimumTaxTypeId"
            control={control}
            rules={{ required: true }}
            defaultValue=""
            render={props => (
              <Dropdown
                style={{ marginBottom: 5, width: "100%" }}
                id="minimumTaxTypeIdInput"
                value={props.value}
                options={minimumTaxTypeSelectItems}
                onChange={e => props.onChange(e.target.value)}
              />
            )}
          />
          {errors.deferredTaxBroughtFoward && (
            <span style={{ fontSize: 12, color: "red" }}>
              Deferred Tax Brought Foward is required
            </span>
          )}
        </div>
        <div className="p-d-flex p-flex-column" style={{ marginBottom: 15 }}>
          <label htmlFor="deferredTaxBroughtFowardInput" style={{ marginBottom: 10 }}>
            Deferred Tax Brought Foward
          </label>
          <Controller
            name="deferredTaxBroughtFoward"
            control={control}
            rules={{ required: true }}
            defaultValue=""
            render={props => (
              <InputText
                style={{ marginBottom: 5, width: "100%" }}
                id="deferredTaxBroughtFowardInput"
                value={props.value}
                onChange={e => props.onChange(e.target.value)}
              />
            )}
          />
          {errors.deferredTaxBroughtFoward && (
            <span style={{ fontSize: 12, color: "red" }}>
              Deferred Tax Brought Foward is required
            </span>
          )}
        </div>
        <div className="p-d-flex p-flex-column" style={{ marginBottom: 15 }}>
          <label htmlFor="monthOfOperationInput" style={{ marginBottom: 10 }}>
            Month(s) of Operation
          </label>
          <Controller
            name="monthOfOperation"
            control={control}
            rules={{ required: true }}
            defaultValue=""
            render={props => (
              <InputText
                style={{ marginBottom: 5, width: "100%" }}
                id="monthOfOperationInput"
                value={props.value}
                onChange={e => props.onChange(e.target.value)}
              />
            )}
          />
          {errors.monthOfOperation && (
            <span style={{ fontSize: 12, color: "red" }}>Month(s) of Operation is required</span>
          )}
        </div>
        <div className="p-d-flex p-flex-column" style={{ marginBottom: 15 }}>
          <label htmlFor="openingYearInput" style={{ marginBottom: 10 }}>
            Closing Financial Year
          </label>
          <Controller
            name="openingYear"
            control={control}
            rules={{ required: true }}
            defaultValue=""
            render={props => (
              <Calendar
                style={{ marginBottom: 5, width: "100%" }}
                id="openingYearInput"
                dateFormat="yy/mm/dd"
                value={props.value}
                onChange={e => props.onChange(e.target.value)}
              />
            )}
          />
          {errors.openingYear && (
            <span style={{ fontSize: 12, color: "red" }}>Opening Financial Year is required</span>
          )}
        </div>
        <div className="p-d-flex p-flex-column" style={{ marginBottom: 15 }}>
          <label htmlFor="companyDescriptionInput" style={{ marginBottom: 10 }}>
            Address
          </label>
          <Controller
            name="companyDescription"
            control={control}
            rules={{ required: true }}
            defaultValue=""
            render={props => (
              <InputText
                style={{ marginBottom: 5, width: "100%" }}
                id="companyDescriptionInput"
                value={props.value}
                onChange={e => props.onChange(e.target.value)}
              />
            )}
          />
          {errors.companyDescription && (
            <span style={{ fontSize: 12, color: "red" }}>Address is required</span>
          )}
        </div>
        <Button
          label={!loading ? "Add Company" : null}
          icon={loading ? "pi pi-spin pi-spinner" : null}
          type="submit"
          style={{ width: "100%", marginTop: 10 }}
        />
      </form>
    </Dialog>
  );
};

export default AddCompanyForm;
